@font-face {
  font-weight: normal;
  font-family: "Panton";
  font-style: normal;
  src: url("../fonts/PantonRegular.woff2") format("woff2"), url("../fonts/PantonRegular.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "Panton";
  font-style: normal;
  src: url("../fonts/PantonSemiBold.woff2") format("woff2"), url("../fonts/PantonSemiBold.woff") format("woff"); }

@font-face {
  font-weight: normal;
  font-family: "PantonBlack";
  font-style: normal;
  src: url("../fonts/PantonBlack.woff2") format("woff2"), url("../fonts/PantonBlack.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "PantonExtraBold";
  font-style: normal;
  src: url("../fonts/PantonExtraBold.woff2") format("woff2"), url("../fonts/PantonExtraBold.woff") format("woff"); }

@font-face {
  font-weight: bold;
  font-family: "PantonBold";
  font-style: normal;
  src: url("../fonts/PantonBold.woff2") format("woff2"), url("../fonts/PantonBold.woff") format("woff"); }

body {
  font-size: 1em;
  line-height: 1em;
  font-family: "Panton", "Arial";
  font-weight: 300;
  color: black;
  font-style: normal;
  background-color: #ffffff; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

@keyframes fade {
  from {
    opacity: 1; }
  to {
    opacity: 0; } }

@keyframes popup {
  from {
    transform: translateY(-1000px); }
  to {
    transform: translateY(0px); } }

.game {
  position: relative;
  background-color: #faf8ef;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh; }

.about {
  display: none;
  position: fixed;
  flex-direction: column;
  justify-content: center;
  margin: 5% 20%;
  z-index: 1;
  width: 60vw;
  height: 60vh;
  background-color: #fc538b;
  border: 10px solid wheat;
  border-radius: 15px;
  animation: popup .4s ease-in-out forwards; }
  .about h3 {
    display: flex;
    justify-content: center;
    align-self: center;
    width: 50%;
    color: white;
    text-align: center;
    font-size: 30px;
    line-height: 30px;
    text-shadow: 2px 2px 2px black; }

.about-icons {
  position: relative;
  align-self: center;
  margin-top: 10%; }
  .about-icons p {
    display: flex;
    justify-content: center;
    align-self: center;
    width: 100%;
    color: white;
    text-align: center;
    font-size: 23px; }

.lose {
  display: none;
  justify-content: center;
  align-items: center; }
  .lose button {
    position: relative;
    z-index: 1;
    padding: 1.1rem 1.5rem;
    font-size: 1.1rem;
    border-radius: 100px;
    border: none;
    color: #fafafa;
    cursor: pointer;
    background: #28d4ff; }
  .lose button:focus {
    outline: none;
    transform: translateY(1px); }
  .lose h3 {
    position: relative;
    top: -10%; }

span {
  line-height: 0;
  position: absolute;
  right: 1rem;
  top: 5rem;
  font-size: 12rem;
  color: black;
  cursor: pointer; }

.field {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  flex-wrap: wrap;
  background-color: white; }

.cell {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  height: 16vh;
  width: 16vh;
  background-color: #bbada0;
  border: 5px solid #cdc1b4;
  outline: 5px solid #cdc1b4;
  box-sizing: border-box; }

.cell.animate {
  padding: 0;
  margin: 0;
  position: absolute;
  transition: left 0.2s, top 0.2s;
  animation-name: fade;
  animation-duration: 0.2s;
  animation-iteration-count: 1;
  animation-fill-mode: forwards; }

.cell[cell-number="2"] {
  background-color: #eee4da; }

.cell[cell-number="4"] {
  background-color: #ede0c8; }

.cell[cell-number="8"] {
  background-color: #fadfae; }

.cell[cell-number="16"] {
  background-color: #ffce72; }

.cell[cell-number="32"] {
  background-color: #fabf51; }

.cell[cell-number="64"] {
  background-color: #ffba82; }

.cell[cell-number="128"] {
  background-color: #ffad4f; }

.cell[cell-number="256"] {
  background-color: #ff8b5d; }

.cell[cell-number="512"] {
  background-color: #ff9684; }

.cell[cell-number="1024"] {
  background-color: #ff6123; }

.cell[cell-number="2048"] {
  background-color: #f55027; }

.cell[cell-number="4096"] {
  background-color: #ff4010; }

.header {
  position: relative;
  display: flex;
  align-items: center;
  background-color: #faf8ef;
  height: 10vh;
  font-size: 36px;
  color: pink;
  text-shadow: 2px 2px 2px black; }
